import React, {useState} from 'react'
import firebase from '../firebase'

const ResetPage = () => {

    const [email, setEmail] = useState('')
    const [header, setHeader] = useState('Восстановить пароль')
    async function resetPassword(email) {
        await firebase.resetPassword(email).then(() => setHeader('Сообщение отправлено на e-mail'))
    }
    return (
        <div className="login-page">
            <h2 className="form-header">{header}</h2>
            <form 
            className="form"
            onSubmit = {e => e.preventDefault() && false}>
                <label className="form-field">
                    <div>E-mail</div>
                    <input id="e-mail" name="e-mail" autoComplete="off" autoFocus value={email} onChange={((e) => setEmail(e.target.value))} />
                </label>
                <div className="form-field">
                <button 
                className="form-button"
                type="submit"
                onClick={() => resetPassword(email)}
                >Восстановить
                </button>
                </div>
            </form>
        </div>
    )
}

export default ResetPage